"use client";

import type { ItineraryData, ItineraryUpdateData } from "@/lib/types";
import { ItineraryCard } from "./ItineraryCard";
import { ScrollArea } from "@/components/ui/scroll-area";
import { useState } from "react";
import {
  AlertDialog,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { ItineraryForm } from "./ItineraryForm";
import { useAuth } from "@/contexts/AuthContext";
import { editItinerary } from "@/lib/firebase/firestore";

interface SavedItinerariesListProps {
  savedItineraries: ItineraryData[];
  onRemove: (id: string) => void;
}

export function SavedItinerariesList({ savedItineraries, onRemove }: SavedItinerariesListProps) {
  const [editingItinerary, setEditingItinerary] = useState<ItineraryData | null>(null);
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false);
  const { user } = useAuth();

  if (savedItineraries.length === 0) {
    return (
      <div className="text-center py-8">
        <p className="text-muted-foreground">You have no saved itineraries yet.</p>
        <p className="text-sm text-muted-foreground">Start planning and save your favorite trips!</p>
      </div>
    );
  }

  // Sort by newest first
  const sortedItineraries = [...savedItineraries].sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());

  return (
    <div className="space-y-6">
      {/* For larger screens, use columns. For smaller, stack them. */}
      <div className="md:columns-2 lg:columns-3 gap-6 space-y-6">
        {sortedItineraries.map((itinerary) => (
          <ItineraryCard
            key={itinerary.id}
            itineraryData={itinerary} // Pass the whole itinerary data
            onRemove={onRemove}
            isSaved={true}
            onEdit={() => {
              setEditingItinerary(itinerary);
              setIsEditDialogOpen(true);
            }}
          />
        ))}
      </div>

      {editingItinerary && (
        <AlertDialog open={isEditDialogOpen} onOpenChange={setIsEditDialogOpen}>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>Edit Itinerary</AlertDialogTitle>
            </AlertDialogHeader>
            <ItineraryForm
              initialData={{
                name: editingItinerary.name,
                days: editingItinerary.days,
                notes: editingItinerary.notes,
              }}
              onSubmit={async (data: ItineraryUpdateData) => {
                if (user && editingItinerary.id) {
                  await editItinerary(user.uid, editingItinerary.id, data);
                  setIsEditDialogOpen(false);
                }
              }}
              submitButtonText="Save Changes"
            />
          </AlertDialogContent>
        </AlertDialog>
      )}
    </div>
  );
}
